import React, { useState } from 'react';
import styles from './ReactSheet.module.css';

function ReactSheetDialog(props) {
  const [open, setOpen] = useState(false);
  const { modal } = props;

  const openSheet = () => {
    setOpen(true);
  };

  const closeSheet = () => {
    setOpen(false);
  };

  const handleEscKey = (event) => {
    if (event.key === 'Escape') {
      closeSheet();
    }
  };

  const trapFocus = (event) => {
    if (!open) {
      return;
    }

    const isTabPressed = event.key === 'Tab' || event.keyCode === 9;

    if (!isTabPressed) {
      return;
    }

    const trapEl = 'button, [href], input, select, textarea, [tabindex]:not([tabindex="-1"])';
    const trapContent = document.querySelectorAll(trapEl);
    const firstTrapEl = trapContent[0];
    const lastTrapEl = trapContent[trapContent.length - 1];

    if (event.shiftKey) {
      if (document.activeElement === firstTrapEl) {
        lastTrapEl.focus();
        event.preventDefault();
      }
    } else {
      if (document.activeElement === lastTrapEl) {
        firstTrapEl.focus();
        event.preventDefault();
      }
    }

    if (!document.getElementById('sheet-heading').contains(event.target)) {
      event.stopPropagation();
      document.getElementById('sheet-heading').focus();
    }
  };

  const modalClass = modal ? styles.modal : '';
  const sheetClass = open ? styles.open : '';

  return (
    <>
      <button id="side-sheet-button" onClick={openSheet}>Open Sheet</button>

      <div
        id="side-sheet"
        className={`${modalClass} ${sheetClass}`}
        role="dialog"
        aria-labelledby="sheet-heading"
        aria-modal={modal}
        aria-hidden={!open}
        onKeyDown={trapFocus}
      >
        <div className="side-sheet-header">
          <h2 id="sheet-heading">
            Sheet Dialog
          </h2>
          <button id="close-side-sheet" onClick={closeSheet}>
            &times;
          </button>
        </div>
        <div className="side-sheet-content">
          <p>This is a {modal ? 'modal' : 'non-modal'} sheet. Use the close button or press the escape key to close it.</p>
          <p>Here are some resources to learn more about creating accessible modal dialogs:</p>
          <ul>
            <li><a href="https://www.w3.org/TR/wai-aria-practices-1.1/#dialog_modal" target="_blank" rel="noopener">WAI-ARIA Authoring Practices: Dialog Modal</a></li>
            <li><a href="https://www.w3.org/WAI/tutorials/dialogs/" target="_blank" rel="noopener">How to create an accessible modal dialog</a></li>
          </ul>
        </div>
      </div>
    </>
  );
}

export default ReactSheetDialog;
